* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  width: 600px;
  margin: auto;
  margin-top: 20px;
  background-color: antiquewhite;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 0 1rem;
}
.btn {
  display: inline;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}
.btn :hover {
  background: #666;
}
